<template>
  <div>
  <section class="hero is-fullheight">
    <!-- Hero header: will stick at the top -->
    <div class="hero-head">
      <header class="nav">
        <div class="container">
          <div class="nav-left">
            <gene-list :options="genes" @select="onOptionSelect"></gene-list>
          </div>
          <span class="nav-toggle">
            <span></span>
            <span></span>
            <span></span>
          </span>
          <div class="nav-right nav-menu">
            <a class="nav-item is-active">
              Home
            </a>
          </div>
        </div>
      </header>
    </div>

    <!-- Hero content: will be in the middle -->
    <div class="hero-body has-text-left">
      <section>
        <div class="container is-fluid">
          <div class="heading">
            <h1 class="title">Gene Name: {{ geneName }}</h1>
          </div>
          <div>
            <p>
            <span v-for="mod in modifications" :class="[mod.classification]">{{ mod.text }}<div v-if="mod.classification === 'modification'" v-html="mod.tooltip" class="tooltiptext"></div></span>
            </p>
            <hr>
            <p>
            <span v-for="piece in allSeq" :style="{color: piece.col}">{{ piece.text }}</span>
            </p>
          </div>
          <pep-table></pep-table>
          <p>
          <span>|</span>
          <span v-for="seq in sequences" class="tooltip"> {{ seq }} |</span>
          </p>
        </div>
        </section>
    </div>

    <!-- Hero footer: will stick at the bottom -->
    <div class="hero-foot">
      <nav class="tabs is-boxed is-fullwidth">
        <div class="container">
          <ul>
            <li class="is-active"><a>Overview</a></li>
            <li><a>Modifiers</a></li>
<!--            <li><a>Grid</a></li>
            <li><a>Elements</a></li>
            <li><a>Components</a></li>
            <li><a>Layout</a></li> -->
          </ul>
        </div>
      </nav>
    </div>
  </section>
  </div>
</template>

<script>
  import _ from 'lodash'
  import GeneList from './gene-list/gene-list.vue'
  import PepTable from './pep-table/pep-table.vue'
  export default {
    name: 'hello',
    data () {
      return {
        msg: 'Welcome to Your Vue.js App',
        all: [{'': -1, SEQ: ['asd'], NP_Counts: 1, Gene_Name: 'aaa001'}],
        selected: [{'': -1, SEQ: ['asd'], NP_Counts: 1, Gene_Name: 'aaa001'}],
        sequences: [],
        geneName: 'AADACL3',
        genes: [],
        fasta_seq: ''
      }
    },
    created () {
      // console.log('created')
      var oReq = new XMLHttpRequest()

      // oReq.addEventListener('progress', updateProgress)
      // oReq.addEventListener('load', transferComplete.call(this))
      // oReq.addEventListener('error', transferFailed)
      // oReq.addEventListener('abort', transferCanceled)

      oReq.open('get', './static/SubsetForTesting.txt', true)
      // oReq.open('get', '/static/Hu_Output_Ver7.txt', true)
      oReq.onreadystatechange = (aEvt) => {
        if (oReq.readyState === XMLHttpRequest.DONE) {
          var temp = oReq.responseText.replace(/\n$/, '').split('\n')
          var keys = temp.shift().split('\t')
          var o = _.map(temp, (t) => _.zipObject(keys, _.map(t.replace(/'/g, '').split('\t'))))
          // _.forEach(_.groupBy(o, s => s.Gene), (v, k) => { console.log(k, _.groupBy(v, t => t.NP_Accession)) })
          this.all = _.reduce(o, (r, v, k) => {
            (r[v.Gene] || (r[v.Gene] = {modifications: [], table: {}, peptides: [], FASTA_SEQ: v.FASTA_SEQ}))
            r[v.Gene].peptides = _.union(r[v.Gene].peptides, v.UNIQ_SEQ.replace(/\s/g, '').split(','))

            r[v.Gene].table[v.protein_pos] || (r[v.Gene].table[v.protein_pos] = '<table class="table is-bordered is-striped is-narrow"><thead><tr><th>protein_alt</th><th>CHROM</th><th>POS</th><th>ALT</th><th>TAC</th></tr></thead><tr>')
            r[v.Gene].table[v.protein_pos] += '<td>' + v.protein_alt + '</td><td>' + v.CHROM + '</td><td>' + v.POS + '</td><td>' + v.ALT + '</td><td>' + _.round(v.alt_TAC_ratio, 3) + '</td></tr>'

            r[v.Gene].modifications.push(
              parseInt(v.protein_pos)
              // protein_ref: v.protein_ref,
              // protein_alt: v.protein_alt,
              // CHROM: v.CHROM,
              // POS: parseInt(v.POS),
              // ALT: v.ALT,
              // alt_TAC_ratio: parseFloat(v.alt_TAC_ratio)
            )
            return r
          }, {})
          // console.log(this.all)
          // console.log(_.map(this.all, s => _.groupBy(s, 'UNIQ_SEQ')))
          // console.log(_.map(this.all, s => _.unionBy(s, t => t.UNIQ_SEQ.split(','))))
          this.genes = _.keys(this.all)
          this.selected = this.all['AADACL3']
          this.fasta_seq = this.selected.FASTA_SEQ
          this.sequences = this.selected.peptides
        }
      }
      oReq.onprogress = function (event) {
        event.loaded
        event.total
        console.log(event.loaded / event.total)
      }
      oReq.send()
    },
    components: {
      GeneList,
      PepTable
    },
    methods: {
      onOptionSelect (option) {
        console.log('Selected option:', option)
        this.geneName = option
        this.selected = this.all[option]
        this.fasta_seq = this.selected.FASTA_SEQ
        this.sequences = this.selected.peptides
      },
      // substrings (w) {
        // const i = this.selected[0].FASTA_SEQ.indexOf(w)
        // if (i !== -1) {
          // const s1 = this.selected[0].FASTA_SEQ.slice(0, i)
          // const s2 = this.selected[0].FASTA_SEQ.slice(i, i + w.length)
          // const s3 = this.selected[0].FASTA_SEQ.slice(i + w.length)
          // this.fasta_seq = [s1, s2, s3]
        // } else {
          // this.resetSS()
        // }
      // },
      resetSS () {
        this.fasta_seq = this.selected.FASTA_SEQ
      }
    },
    computed: {
      allSeq () {
        // return [{col: 'none', text: 'B'}, {col: 'orange', text: 'O'}, {col: 'red', text: 'R'}]
        const positions = _.chain(this.sequences).uniq().map(s => { const i = this.fasta_seq.indexOf(s); return [i, i + s.length] }).sort((a, b) => a[0] - b[0]).value()
        var res = []
        var tempSeq = this.fasta_seq
        _.forEachRight(positions, s => {
          if (s[0] !== -1) {
            res.push({col: '#2c3e50', text: tempSeq.slice(s[1])})
            res.push({col: 'orange', text: '[' + tempSeq.slice(s[0], s[1]) + ']'})
            tempSeq = tempSeq.slice(0, s[0])
          }
        })
        res.push({col: '#2c3e50', text: tempSeq})
        return res.reverse()
      },
      modifications () {
        // return [{col: 'none', text: 'B'}, {col: 'orange', text: 'O'}, {col: 'red', text: 'R'}]
        const positions = _.uniq(this.selected.modifications).sort((a, b) => a - b)
        // console.log(_.map(positions, s => [s.protein_pos, s.protein_ref]))
        var res = []
        var tempSeq = this.fasta_seq
        _.forEachRight(positions, s => {
          res.push({text: tempSeq.slice(s), classification: 'normal'})
          res.push({text: tempSeq.slice(s - 1, s),
            tooltip: this.selected.table[s],
            classification: 'modification'
          })
          tempSeq = tempSeq.slice(0, s - 1)
        })
        res.push({classification: 'normal', text: tempSeq})
        return res.reverse()
      }
      // substrings () {
        // const seq = this.selected[0].FASTA_SEQ
        // return _.map(this.sequences, s => {
          // const start = seq.indexOf(s)
          // return { start: start, end: start + s.length, seq: s }
        // })
      // }
    }
  }
  </script>

  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped>
  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }
  .hero-body {
    align-items: flex-start !important;
  }
  .main-content {
    background: white;
    color: black;
  }

  span {
    word-break: break-all;
  }

  .normal {
    color: #2c3e50;
  }

  .modification .tooltiptext {
    display:block;
    visibility: hidden;
    width: 400px;
    /* background-color: #555;
    color: #fff;
    border-radius: 6px;
    padding: 5px 0; */
    position: absolute;
    z-index: 1;
    bottom: 125%;
    left: 50%;
    margin-left: -60px;
    opacity: 0;
    transition: opacity 1s;
    z-index: 999;
  }

  .modification {
    color: purple ;
    font-weight: bold;
    position: relative;
    display: inline-block;
  }

  .modification .tooltiptext::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    /* border-width: 5px
    border-style: solid;
    border-color: #555 transparent transparent transparent; */
  }

  .modification:hover .tooltiptext {
    visibility: visible;
    opacity: 1;
  }
  </style>



